.navbar {
  text-align: center;
  display: flex;
  font-family: 'Arial Narrow', Arial, sans-serif, Helvetica, sans-serif;
  text-transform: uppercase;
  height: 100px;
}

.navbar h1{
  font-size: 26px;
  margin: auto auto;
  left: 0;
  right: 0;
  top: 10px;
  font-weight: normal;
  position: absolute;
}

.links {
  font-size: 10px;
  position: absolute;
  display: flex;
  position: absolute;
  right: 30px;
  top: 19px;
}

.links a:first-child {
  margin-right: 40px;
}